/* eslint-disable */
import { collection, doc, onSnapshot } from 'firebase/firestore';
import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { connectDB } from '../database';

const ContBox = styled.button`
  padding: 1em;
  margin: 1em 0;
  border: 2px solid lightslategrey;
  border-radius: 1em;
`;


const Contents = () => {
  const [content, setContents] = useState([]);
  const contentsRef = collection(connectDB, "");
  
  const [miniWindow, setMiniWindow] = useState(false);
  const [windowTitle, setWindowTitle] = useState("");
  const [windowText, setWindowText] = useState("");
  const [windowDate, setWindowDate] = useState("");
  const toggleState = (state) => setMiniWindow(!state);
  useEffect(() => {
    onSnapshot(contentsRef, (snaps) => setContents(snaps.docs.map((doc) => ({id: doc.id, ...doc.data()}))));
  }, []);

  const onClickWindow = (index) => {
    toggleState(false);
    const {title, text, date} = content[index];
    setWindowTitle(title);
    setWindowText(text);
    setWindowDate(date);
  }

  return <main>
    <h2>MasterPiece and Life</h2>
    <p>, , </p>
    {
      miniWindow &&
      <div className='test'>
        <div className='test2'>
          <button onClick={() => toggleState(true)}></button>
          {/* <button></button> */}
          <h4>{windowTitle}</h4>
          <p>{windowText}</p>
          <p>{windowDate}</p>
        </div>
      </div>
    }
    <div>
    {
      content.map((item, index) => {
        const {title, text, date} = item;
        return <ContBox key={index} onClick={()=>onClickWindow(index)}>
          <h4>{title}</h4>
          <p>{text}</p>
          <p>{date}</p>
        </ContBox>
      })
    }
    </div>
  </main>
}

export default Contents;